import axios from 'axios';

const ENDPOINT_URL = 'https://api.geneea.com/v3/analysis';
const KEY = 'ba0d8598e8acaa705ea7d1fd42fd6554';

export const fetchAnalysisData = (text) => {
  return axios.post(
    ENDPOINT_URL,
    { id: 1, analyses: ['entities'], text },
    { headers: { Authorization: `user_key ${KEY}`, 'Content-Type': 'application/json'} }
  );
};
