import React from 'react';
import styled from 'styled-components';

const TableStyled = styled.table`
  width: 75%;
  color: #24292e;
  border-collapse: collapse;
  
  & thead {
    font-size: 2rem;
    & tr { 
      border-bottom: 2px solid #24292e;
    }
  }
  & tbody {
    font-size: 1.5rem;
    
    & td {
      text-align: center;
      padding: 0.5rem 1rem;
      width: 50%;
      border: 1px solid #24292e;
    }
  }
`;

const Table = ({ entities }) => (
  <TableStyled>
    <thead>
      <tr>
        <th>Text</th>
        <th>Type</th>
      </tr>
    </thead>
    <tbody>
      {entities.map((entity) => (
        <tr key={entity.id}>
          <td>{entity.stdForm}</td>
          <td>{entity.type}</td>
        </tr>
      ))}
    </tbody>
  </TableStyled>
);

export default Table;
