import React, { useReducer, createContext } from 'react';

const DEFAULT_STATE = { data: null, error: null };

const ACTIONS = {
  SET_DATA: 'SET_DATA',
  SET_ERROR: 'SET_ERROR',
  RESET: 'RESET',
};

export const GlobalStoreContext = createContext({ ...DEFAULT_STATE });

export const GlobalStoreProvider = ({ children }) => {
  const reducer = (state, action) => {
    switch (action.type) {
      case ACTIONS.SET_ERROR:
        return {
          ...state,
          error: action.error,
        };
      case ACTIONS.SET_DATA:
        return {
          ...state,
          data: action.data,
        };
      case ACTIONS.RESET:
      default:
        return DEFAULT_STATE;
    }
  };

  const [state, dispatch] = useReducer(reducer, { ...DEFAULT_STATE }, undefined);

  const setData = (data) => dispatch({ type: ACTIONS.SET_DATA, data });
  const setError = (error) => {
    dispatch({ type: ACTIONS.SET_ERROR, error });
  };
  const resetStore = () => dispatch({ type: ACTIONS.RESET });

  return (
    <GlobalStoreContext.Provider value={{ ...state, setData, setError, resetStore }}>
      {children}
    </GlobalStoreContext.Provider>
  );
};
