import React, { useContext } from 'react';
import { Formik } from 'formik';
import * as Yup from 'yup';
import styled from 'styled-components';

import { fetchAnalysisData } from '../core/api';
import { GlobalStoreContext } from '../core/store';

const FormStyled = styled.form`
  width: 100%;
  margin: 1rem 0;
`;

const InputStyled = styled.textarea`
  display: inline-block;
  width: 50%;
  max-width: 1024px;
  min-height: 8rem;
  padding: 1rem 0.5rem;
  border: 2px solid #24292e;
  border-radius: 0.25rem;
  font-size: 1.5rem;
  margin-bottom: 0.25rem;
  resize: vertical;

  @media (max-width: 768px) {
    width: 90%;
  }
`;

const ErrorStyled = styled.p`
  color: red;
  font-size: 1.5rem;
  font-weight: bold;
`;

const ButtonStyled = styled.button`
  margin-top: 0.5rem;
`;

const SearchBar = () => {
  const { setData } = useContext(GlobalStoreContext);
  return (
    <Formik
      initialValues={{ text: '' }}
      validationSchema={Yup.object().shape({
        text: Yup.string().required('Cannot be empty!'),
      })}
      onSubmit={async (values, { setSubmitting, setErrors }) => {
        const text = values.text;
        fetchAnalysisData(text)
          .then((res) => {
            setData(res.data.entities);
          })
          .catch((e) => setErrors({ text: e }))
          .finally(() => setSubmitting(false));
      }}
    >
      {({ values, errors, isSubmitting, handleChange, handleBlur, handleSubmit }) => (
        <FormStyled onSubmit={handleSubmit}>
          <InputStyled
            name="text"
            value={values.text}
            onChange={handleChange}
            onBlur={handleBlur}
            disabled={!!isSubmitting}
          />
          <ErrorStyled>{errors.text}</ErrorStyled>
          <ButtonStyled type="submit" disabled={!values.text.length || errors.text}>
            Search!
          </ButtonStyled>
        </FormStyled>
      )}
    </Formik>
  );
};

export default SearchBar;
